import Link from "next/link";
import Image from "next/image";

type Article = {
    _id: string;
    title: string;
    slug: string;
    cover?: { url?: string; asset?: { url?: string } };
};

export default function Grid6Small({ items }: { items: Article[] }) {
    // Define different aspect ratios for mosaic effect
    const aspectRatios = [
        'aspect-[4/3]',     // Standard
        'aspect-[4/5]',     // Portrait
        'aspect-[3/2]',     // Wide
        'aspect-[3/4]',     // Tall portrait
        'aspect-[5/4]',     // Landscape
        'aspect-[4/3]',     // Standard
    ];

    return (
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-x-4 sm:gap-x-6 md:gap-x-8 lg:gap-x-10 gap-y-6 sm:gap-y-8 md:gap-y-10 lg:gap-y-12 bg-background">
            {items.slice(0, 6).map((p, index) => {
                const img = p.cover?.asset?.url ?? p.cover?.url;
                const aspectRatio = aspectRatios[index] || 'aspect-[4/3]';

                return (
                    <Link
                        key={p._id}
                        href={`/articles/${p.slug}`}
                        className="group block overflow-hidden rounded-lg transition hover:opacity-90"
                    >
                        {img && (
                            <div className={`relative ${aspectRatio} overflow-hidden rounded-lg`}>
                                <Image
                                    src={img}
                                    alt={p.title}
                                    fill
                                    sizes="(min-width:1024px) 33vw, (min-width:640px) 50vw, 100vw"
                                    className="object-cover transition-transform duration-300 group-hover:scale-[1.03]"
                                />
                            </div>
                        )}
                        <div className="pt-3">
                            <h3 className="text-sm font-medium text-foreground leading-snug">
                                {p.title}
                            </h3>
                        </div>
                    </Link>
                );
            })}
        </div>
    );
}
